import React, { Component } from 'react';
import ItemsSection, {ItemsSectionBody,ItemsSectionTitle} from '../ItemsSection'

class MyListItemsSection extends Component {
  constructor()
  {
    super()

    this.state = {
      loading: true,
      myList: [],
      type: "grid"
    }
  }

  componentDidMount()
  {
    this.setState({
      loading: false,
      myList: [],
      type: "grid"
    })
  }

  render() {
    const {loading,myList,type} = this.state
    return (

      <ItemsSection>
        <ItemsSectionTitle title="Mi Lista">
          <a href="">Ver todas</a>
        </ItemsSectionTitle>
        <ItemsSectionBody loadingMessage="No hay items en Mi Lista" loading={loading} type={type} items={myList}></ItemsSectionBody>
      </ItemsSection>

      )
    }
}

export default MyListItemsSection;



