import React, { Component } from 'react';

class FilterRight extends Component {
  render() {
    const {viewTypeChange, type} = this.props
    let gridActive = type === "grid" ? "btn btn-light active" : "btn btn-light"
    let listActive = type === "list" ? "btn btn-light active" : "btn btn-light"

    return (
      <div className="filters-bar-right">
        <a onClick={() => (viewTypeChange("grid"))} href="peliculas-grid.html" className={gridActive}>
          <i className="mdi mdi-view-grid" aria-hidden="true"></i>
        </a>
        <a onClick={() => (viewTypeChange("list"))}href="peliculas-list.html" className={listActive}>
          <i className="mdi mdi-view-list" aria-hidden="true"></i>
        </a>
      </div>
    )}
}

export default FilterRight;

FilterRight.defaultProps = {
  type: "grid"
}



