import React, { Component } from 'react';
import PropTypes from 'prop-types'
import {GridItemBody,GridItemDate,GridItemTitle,GridItemActions} from '../GridItem'

class GridItem extends Component {
  render() {
    return (
        <article className="col-md-2">
          <div className="grid-item">
            {(this.props.image) ?
              <img className="img-fluid" src={this.props.image} alt="Movie" /> :
              <img className="img-fluid" src="http://via.placeholder.com/160x220" alt="Movie" />
            }
            
            <GridItemBody>
              <GridItemTitle>{this.props.title}</GridItemTitle>
              <GridItemDate>{this.props.date}</GridItemDate>
              <GridItemActions>{this.props.children}</GridItemActions>
            </GridItemBody>
          </div>
        </article>
      );
    }
}

export default GridItem;

GridItem.propTypes = {
  image: PropTypes.string,
  title: PropTypes.string.isRequired,
  date: PropTypes.string
}

GridItem.defaultProps = {
  image: "http://via.placeholder.com/160x220"
}



