import React from 'react';
import PropTypes from 'prop-types'

const ListItem = ({image,title,description,children}) => (
      <article className="col-md-6">
        <div className="list-item">
          <div className="list-item-img">
            {(image) ? <img className="img-fluid" src={image} alt="Movie" /> :
            <img className="img-fluid" src="http://via.placeholder.com/180x180" alt="Movie" />
            }
          </div>
          <div className="list-item-body">
            <h3 className="list-item-title">{title}</h3>
            <div className="list-item-description">
              <p>{description}</p>
            </div>
            <div className="list-item-actions">
              {children}
              {/* <Button title="Agregar a Mi Lista" type='primary' active={true} icon="mdi mdi-heart-outline" /> */}
            </div>
          </div>
        </div>
      </article>
    )

export default ListItem;

ListItem.propTypes = {
  image : PropTypes.string,
  title : PropTypes.string.isRequired,
  description : PropTypes.string
}



