import React, { Component } from 'react';
import ItemsSection, {ItemsSectionBody,ItemsSectionTitle} from '../ItemsSection'
import TheMovieDbApi from '../../services/TheMovieDbApi'

class PopularSeriesItemsSection extends Component {
  constructor()
  {
    super()
    this.api = new TheMovieDbApi()

    this.state = {
      loading: true,
      items: [],
      type: "grid"
    }
  }

  componentDidMount()
  {
    this.api.getPopularSeries().then(res => {
      if (res.status !== 200)
        return

      let series= [];
      res.data.results.map(serie => (
        series.push({title: serie.name,
           description: serie.overview,
           date: serie.first_air_date,
           image: 'https://image.tmdb.org/t/p/w500/' + serie.poster_path
         })
      ))

      this.setState({
        loading: false,
        items: series.slice(0,6),
        type: "grid"
      })
    })
  }

  render() {
    const {loading,items,type} = this.state

    return (
      <ItemsSection>
        <ItemsSectionTitle title="Series más Populares">
          <a href="">Ver todas</a>
        </ItemsSectionTitle>
        <ItemsSectionBody loadingMessage="No se encontraron Series Populares" loading={loading} type={type} items={items}></ItemsSectionBody>
      </ItemsSection>

      )
    }
}

export default PopularSeriesItemsSection;



