import React from 'react';

const Filter = ({name,options}) =>
(
  <select name={"filter-"+name} id={"filter-"+name} onChange={(e) => {this.props.filterChange(name,e)}} className="form-control">
    { options.map((option,ix) => (
      <option value={option} key={ix}>{option}</option>
    ))}
  </select>
)

export default Filter



