import React, { Component } from 'react';
import FiltersBar, {Filter, FilterLeft,FilterRight} from '../components/FiltersBar'
import PrimaryTitle from '../components/PrimaryTitle'
import MoviesItemsSection from '../components/MoviesItemsSection'
import TheMovieDbApi from '../services/TheMovieDbApi'
import LayoutContainer from '../components/LayoutContainer'

class MoviePage extends Component {
  constructor()
  {
    super()
    this.api = new TheMovieDbApi()
    this.state = {
      genres: [],
      movieItemSectionViewType: "grid",
    }
  }

  componentDidMount()
  {
    this.api.getMoviesGenres().then(res => {
      if (res.status !== 200)
        return
      let genres= [];
      res.data.genres.map(genre => (
        genres.push(genre.name)
      ))
      this.setState({
        genres: genres
      })
    })
  }

  render() {
    let defaultYearsFilter = ['2010','2011','2012','2013','2014','2015','2016','2017','2018']
    let defaultOrderByFilter = [ 'popularity.asc',
      'release_date.asc',
      'release_date.desc',
      'revenue.asc',
      'revenue.desc',
      'primary_release_date.asc',
      'primary_release_date.desc',
      'original_title.asc',
      'original_title.desc',
      'vote_average.asc',
      'vote_average.desc',
      'vote_count.asc',
      'vote_count.desc',
      'popularity.desc'
    ]

    const handleViewTypeChange = (type) => {
      this.setState({
          movieItemSectionViewType: type
      })
    }

    const {genres,movieItemSectionViewType} = this.state

    return (
      <LayoutContainer>
        <PrimaryTitle>Películas</PrimaryTitle>
        <FiltersBar>
          <FilterLeft>
            <Filter name="year" options={defaultYearsFilter}></Filter>
            <Filter name="order" options={defaultOrderByFilter}></Filter>
            <Filter name="genres" options={genres} ></Filter>
          </FilterLeft>
          <FilterRight viewTypeChange={handleViewTypeChange}></FilterRight>
        </FiltersBar>
        <MoviesItemsSection type={movieItemSectionViewType}/>
      </LayoutContainer>
      );
    }
}

export default MoviePage;



