import React, { Component } from 'react';
import PopularMoviesItemsSection from '../components/PopularMoviesItemsSection'
import PopularSeriesItemsSection from '../components/PopularSeriesItemsSection'
import MyListItemsSection from '../components/MyListItemsSection'
import LayoutContainer from '../components/LayoutContainer'

class HomePage extends Component {
  render() {
    return (
      <LayoutContainer>
        <MyListItemsSection/>
        <PopularMoviesItemsSection/>
        <PopularSeriesItemsSection/>
      </LayoutContainer>
    );
  }
}

export default HomePage;



