import React, { Component } from 'react';
import '../App.css';
import Navbar from '../components/Navbar'
import HomePage from './HomePage'
import MoviePage from './MoviePage'

class Layout extends Component {
  render() {
    return (
      <div className="app">
        <header>
          <Navbar/>
        </header>
        <main role="main">
          <MoviePage/>
        </main>
      </div>
    );
  }
}

export default Layout;



