import React from 'react';
import GridItem from '../../GridItem'
import ListItem from '../../ListItem'
import ItemsSectionLoading from './ItemsSectionLoading'
import PropTypes from 'prop-types'

const ItemsSectionBody = ({type,items,loadingMessage,loading}) => (
    <div className="items-section-body">
      {loading && <ItemsSectionLoading/>}

      {(!loading && (type === "grid")) &&
        <div className="row">
          {items.map((movie,ix) => {
            return (<GridItem image={movie.image} title={movie.title} description={movie.description} date={movie.date} key={ix}>
            </GridItem>)
          })}
        </div>
      }

      {
        (!loading && (type === "list")) &&
        <div className="row">
          {
            items.map((movie,ix) => {
              return <ListItem image={movie.image} title={movie.title} description={movie.description} key={ix}/>
            })
          }
        </div>
      }

      { (!items && !loading) &&
        <ItemsSectionLoading message={loadingMessage} />
      }


    </div>
  )

export default ItemsSectionBody


ItemsSectionBody.propTypes = {
  items : PropTypes.arrayOf(
    PropTypes.shape({
      image: PropTypes.string,
      title: PropTypes.string.isRequired,
      description: PropTypes.string,
      date : PropTypes.string
    })
  ).isRequired,
  type: PropTypes.oneOf(['grid', 'list']).isRequired,
}

ItemsSectionBody.defaultProps = {
  loading: true
}



