import React, { Component } from 'react';
import ItemsSection, {ItemsSectionBody,ItemsSectionTitle} from '../ItemsSection'
import TheMovieDbApi from '../../services/TheMovieDbApi'

class PopularMoviesItemsSection extends Component {
  constructor()
  {
    super()
    this.api = new TheMovieDbApi()

    this.state = {
      loading: true,
      items: [],
      type: "grid"
    }
  }

  componentDidMount()
  {
    this.api.getPopularMovies().then(res => {
      if (res.status !== 200)
        return

      let movies= [];
      res.data.results.map(movie => (
        movies.push({title: movie.title,
           description: movie.overview,
           date: movie.release_date,
           image: 'https://image.tmdb.org/t/p/w500/' + movie.poster_path
         })
      ))

      this.setState({
        loading: false,
        items: movies.slice(0,6),
        type: "grid"
      })
    })
  }

  render() {
    const {loading,items,type} = this.state

    return (

      <ItemsSection>
        <ItemsSectionTitle title="Películas más Populares">
          <a href="">Ver todas</a>
        </ItemsSectionTitle>
        <ItemsSectionBody loadingMessage="No se encontraron Peliculas Populares" loading={loading} type={type} items={items}></ItemsSectionBody>
      </ItemsSection>

      )
    }
}

export default PopularMoviesItemsSection;



